function fetchPokemon(name) {
  return fetch(`https://pokeapi.co/api/v2/pokemon/${name}`).then(response => {
    if (response.ok) {
      return response.json();
    }

    return Promise.reject(new Error(`–ù–µ—Ç –ø–æ–∫–µ–º–æ–Ω–∞ —Å –∏–º–µ–Ω–µ–º ${name}`));
  });
}

const api = {
  fetchPokemon,
};

export default api;
